@import "../../global.scss";

.topbar{
    height: 70px;
    width: 100%;
    background-color: $mainColor;
    color: rgba(0, 0, 0, 0.637);
    position: fixed;
    top: 0;
    z-index: 3;
    transition: all 1s ease;

    .warpper{
        padding: 10px 30px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        

        .left{
            display: flex;
            align-items: center;
            

            .logo{
                font-size: 40px;
                font-weight: 700;
                text-decoration: none;
                color: inherit;
                margin-right: 40px;
                 }
            .itemcontainer{
                display: flex;
                align-items: center;
                margin-left: 30px;

                @include mobile{
                    display: none;
                }

                .icon{
                    font-size: 23px;
                    margin-right: 5px;
                }
                span{
                    font-size: 20px;
                    font-weight: 500;
                }

            } 
        }
        .right{
            .hamburger{
                width: 22px;
                height: 19px;
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                cursor: pointer;
                span{
                    width: 100%;
                    height: 3px;
                    background-color:rgba(0, 0, 0, 0.637);
                    transform-origin: left;
                    transition: all 1s ease;
                }       
            }
        }
    }

    &.active{
        background-color: $mainColor;
        color: white;
        .hamburger{
            span{
                &:first-child{
                    background-color: white;
                    transform: rotate(45deg);
                }
                &:nth-child(2){
                    opacity: 0;
                }
                &:last-child{
                    background-color: white;
                    transform: rotate(-45deg);
                }
            }
        }
    }
}